import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  Grid,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import DeleteIcon from '@mui/icons-material/Delete';

const OrderForm = () => {
  const navigate = useNavigate();
  const [pizzas, setPizzas] = useState([]);
  const [orderItems, setOrderItems] = useState([]);
  const [formData, setFormData] = useState({
    customerName: '',
    customerPhone: '',
    deliveryAddress: '',
  });

  useEffect(() => {
    fetchPizzas();
  }, []);

  const fetchPizzas = async () => {
    try {
      const response = await axios.get('http://localhost:8080/api/pizzas/available');
      setPizzas(response.data);
    } catch (error) {
      console.error('피자 목록을 불러오는데 실패했습니다:', error);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddItem = () => {
    setOrderItems(prev => [...prev, {
      pizza: null,
      quantity: 1,
      size: 'MEDIUM',
      price: 0
    }]);
  };

  const handleRemoveItem = (index) => {
    setOrderItems(prev => prev.filter((_, i) => i !== index));
  };

  const handleItemChange = (index, field, value) => {
    setOrderItems(prev => {
      const newItems = [...prev];
      if (field === 'pizza') {
        const pizza = pizzas.find(p => p.id === value);
        newItems[index] = {
          ...newItems[index],
          pizza: pizza,
          price: pizza ? pizza.basePrice : 0
        };
      } else if (field === 'quantity') {
        newItems[index] = {
          ...newItems[index],
          quantity: value,
          price: newItems[index].pizza ? newItems[index].pizza.basePrice * value : 0
        };
      } else if (field === 'size') {
        const sizeMultiplier = {
          'SMALL': 1.0,
          'MEDIUM': 1.2,
          'LARGE': 1.5
        }[value] || 1.0;
        
        newItems[index] = {
          ...newItems[index],
          size: value,
          price: newItems[index].pizza ? 
            newItems[index].pizza.basePrice * newItems[index].quantity * sizeMultiplier : 0
        };
      }
      return newItems;
    });
  };

  const calculateTotal = () => {
    return orderItems.reduce((total, item) => total + item.price, 0);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const orderData = {
        customerName: formData.customerName,
        customerPhone: formData.customerPhone,
        deliveryAddress: formData.deliveryAddress,
        orderItems: orderItems.map(item => ({
          pizza: { id: item.pizza.id },
          size: item.size,
          quantity: item.quantity,
          price: item.price || 0,
          cost: item.cost || 0
        }))
      };

      const response = await fetch('/api/orders', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(orderData),
      });

      if (!response.ok) {
        throw new Error('주문 생성에 실패했습니다.');
      }

      const result = await response.json();
      console.log('주문이 성공적으로 생성되었습니다:', result);
      
      // 주문 성공 후 폼 초기화
      setFormData({
        customerName: '',
        customerPhone: '',
        deliveryAddress: '',
      });
      setOrderItems([]);
      
    } catch (error) {
      console.error('주문 생성 중 오류 발생:', error);
      alert('주문 생성 중 오류가 발생했습니다.');
    }
  };

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ maxWidth: 800, mx: 'auto', p: 3 }}>
      <Typography variant="h4" gutterBottom>새 주문</Typography>
      
      <Paper sx={{ p: 3, mb: 3 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label="고객명"
              name="customerName"
              value={formData.customerName}
              onChange={handleInputChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label="연락처"
              name="customerPhone"
              value={formData.customerPhone}
              onChange={handleInputChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              label="배송지"
              name="deliveryAddress"
              value={formData.deliveryAddress}
              onChange={handleInputChange}
            />
          </Grid>
        </Grid>
      </Paper>

      <Paper sx={{ p: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6">주문 항목</Typography>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={handleAddItem}
          >
            항목 추가
          </Button>
        </Box>

        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>피자</TableCell>
                <TableCell>수량</TableCell>
                <TableCell>사이즈</TableCell>
                <TableCell>가격</TableCell>
                <TableCell>작업</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {orderItems.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <FormControl fullWidth>
                      <Select
                        value={item.pizza?.id || ''}
                        onChange={(e) => handleItemChange(index, 'pizza', e.target.value)}
                      >
                        <MenuItem value="">피자를 선택하세요</MenuItem>
                        {pizzas.map(pizza => (
                          <MenuItem key={pizza.id} value={pizza.id}>
                            {pizza.name} ({pizza.basePrice.toLocaleString()}원)
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                  </TableCell>
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <IconButton onClick={() => handleItemChange(index, 'quantity', Math.max(1, item.quantity - 1))}>
                        <RemoveIcon />
                      </IconButton>
                      <Typography sx={{ mx: 2 }}>{item.quantity}</Typography>
                      <IconButton onClick={() => handleItemChange(index, 'quantity', item.quantity + 1)}>
                        <AddIcon />
                      </IconButton>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <FormControl fullWidth variant="outlined">
                      <InputLabel>사이즈</InputLabel>
                      <Select
                        label="사이즈"
                        value={item.size || 'MEDIUM'}
                        onChange={(e) => handleItemChange(index, 'size', e.target.value)}
                      >
                        <MenuItem value="SMALL">SMALL (기본가)</MenuItem>
                        <MenuItem value="MEDIUM">MEDIUM (+20%)</MenuItem>
                        <MenuItem value="LARGE">LARGE (+50%)</MenuItem>
                      </Select>
                    </FormControl>
                  </TableCell>
                  <TableCell>{item.price.toLocaleString()}원</TableCell>
                  <TableCell>
                    <IconButton onClick={() => handleRemoveItem(index)}>
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h6">
          총액: {calculateTotal().toLocaleString()}원
        </Typography>
        <Box>
          <Button
            variant="outlined"
            onClick={() => navigate('/orders')}
            sx={{ mr: 2 }}
          >
            취소
          </Button>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            disabled={orderItems.length === 0}
          >
            주문하기
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default OrderForm; 